import React, { Component } from 'react'
import ReactAnime from 'react-animejs'
import '../../Styles/OtherWebsiteStyle.scss';

export class OtherWebsite extends Component {
    render() {
        const {Anime} = ReactAnime
        return (
            <div>
                <Anime 
                    initial={[
                        {
                            targets:'#renovation_text',
                            opacity: 1,
                            easing: 'easeInOutQuad',
                            delay: 2000
                        }
                    ]}
                />
                <div id="renovation_text">
                    <h1>Page currently on renovation</h1>
                    <p>You can check these instead!</p>
                </div>
            </div>
        )
    }
}

export default OtherWebsite
