import React, { Component } from 'react';
import ReactAnime from 'react-animejs'
import '../../Styles/SVGStyle.scss';

export class SVG extends Component {
    render() {
        const {Anime} = ReactAnime
        return (
            <div>
                <Anime
                    animeConfig={{
                        duration: 2000,
                        easing: "easeOutExpo"
                    }}
                    
                    initial={[
                        {             
                            targets:'#launching_rocket',
                            opacity: 0,
                            rotate: 90,
                            translateX:-30,
                            translateY: 1000,
                            delay: -1500
                        },
                        {
                            targets:'#under_renovation',
                            opacity: 0,
                            delay: -1900
                        },
                        {
                            targets:'#launching_rocket',
                            translateY: -90,
                            opacity: 1,
                            easing: 'easeInOutQuad',
                            delay: -500
                        },
                        {
                            targets:'#under_renovation',
                            opacity: 1,
                            easing: 'easeInOutQuad',
                            delay: -1200
                        },
                        {
                            targets:'#launching_rocket',
                            rotate: -45,
                            translateX: -70,
                            begin: function() {
                                document.getElementById("under_renovation").style.zIndex = "1";
                            },
                            easing: 'easeInOutQuad',
                            delay: -900
                        }
                    ]}
                />
                <Anime 
                    initial={[
                        {
                            targets:'#rocket, #big_window, #small_window',
                            translateX: [-2, 2],
                            easing: 'easeInQuad',
                            direction: 'alternate',
                            duration : 1000,
                            loop: true
                        }
                    ]}
                />
                <Anime 
                    initial={[
                        {
                            targets:'#big_flame, #med_flame, #lil_flame',
                            translateY: [-8, 0, 3],
                            easing: 'easeInBounce',
                            duration : 950,
                            loop: true

                        }
                    ]}
                />
                    <h1 id="under_renovation">Under Renovation</h1>
                    <svg 
                        version="1.1" 
                        id="launching_rocket" 
                        x="0px" y="0px"
                        width="100px" height="255px" 
                        viewBox="0 0 100 255" 
                        enable-background="new 0 0 100 255">
                        <path id="big_flame" fill="#F15A29" stroke="#000000" stroke-width="3" stroke-miterlimit="10" d="M38.164,160.965L17.89,219.971
                        c0,0-6.451,9.605,12.44-0.762c0,0,14.437,15.398,13.208,29.121c-1.229,13.721,13.261-4.727,20.173-24.395
                        c1.979-5.631,21.448,12.959,17.301,2.896c-8.242-20.004-20.426-65.867-20.426-65.867H38.164z"
                        />
                        <path id="med_flame" fill="#FBB040" stroke="#000000" stroke-width="3" stroke-miterlimit="10" d="M42.31,160.965c0,0-21.963,48.332,7.833,66.875
                        c0,0,21.799-19.543,10.137-66.264C45.716,103.232,42.31,160.965,42.31,160.965z"
                        />
                        <path id="lil_flame" fill="#F9ED32" stroke="#000000" stroke-width="3" stroke-miterlimit="10" d="M45.988,147.547c0,0-12.918,48.332,4.607,66.873
                        c0,0,12.821-19.541,5.962-66.262C47.992,89.813,45.988,147.547,45.988,147.547z"
                        />
                        <g id="rocket">
                            <path fill="#FFFFFF" stroke="#000000" stroke-width="3" stroke-miterlimit="10" d="M13.743,185.971c0,0,34.096-40.557,69.728,0
                                l12.594,14.941c0,0-16.894-197.901-46.075-197.901c0,0-35.938,8.843-46.075,194.853L13.743,185.971z"/>
                            <path fill="#ED1C24" stroke="#000000" stroke-width="3" stroke-miterlimit="10" d="M85.989,115.172c0,0-2.171,48.082-14.535,59.955
                                c0,0,20.867,28.271,24.61,30.216C102.54,208.71,93.146,119.19,85.989,115.172z"/>
                            <path fill="#ED1C24" stroke="#000000" stroke-width="3" stroke-miterlimit="10" d="M11.548,113.563c0,0,4.617,49.079,16.098,60.954
                                c0,0-16.377,19.945-24.584,27.045C-0.719,204.832,3.914,122.24,11.548,113.563z"/>
                            <path fill="#ED1C24" stroke="#000000" stroke-width="2" stroke-miterlimit="10" d="M32.038,27.595c0,0,21.066-20.084,33.354-5.532
                                c0,0-6.879-19.586-15.402-19.053C42.72,3.465,32.038,27.595,32.038,27.595z"/>
                            <path fill="#ED1C24" stroke="#000000" stroke-width="2" stroke-miterlimit="10" d="M36.27,117.971c0,0,12.772-19.872,29.282,0.102
                                l-15.666,71.759L36.27,117.971z"/>
                            <path opacity="0.2" d="M65.392,21.008c0,0-31.651,100.862-50.25,117.768c0,0,3.883,30.082,12.505,35.742
                                c0,0,26.161-15.721,43.808,0.609c0,0,20.232-26.844,14.535-59.955C70.379,24.463,65.392,21.008,65.392,21.008z"/>
                        </g>
                        <g id="big_window">
                            <ellipse fill="#ED1C24" stroke="#000000" stroke-width="2" stroke-miterlimit="10" cx="49.989" cy="45.092" rx="14.13" ry="14.027"/>
                            <ellipse fill="#1C75BC" stroke="#000000" stroke-width="2" stroke-miterlimit="10" cx="49.989" cy="45.092" rx="11.213" ry="11.131"/>
                            <path fill="#27AAE1" stroke="#000000" stroke-width="2" stroke-miterlimit="10" d="M56.968,36.381l-16.53,14.542
                                c0,0,1.558,2.327,3.006,3.205c1.449,0.879,15.108-16.22,15.108-16.22C57.771,36.96,56.968,36.381,56.968,36.381z"/>
                        </g>
                        <g id="small_window">
                            <ellipse fill="#ED1C24" stroke="#000000" stroke-width="2" stroke-miterlimit="10" cx="49.989" cy="77.415" rx="10.137" ry="10.063"/>
                            <ellipse fill="#1C75BC" stroke="#000000" stroke-width="2" stroke-miterlimit="10" cx="49.989" cy="77.415" rx="6.983" ry="6.932"/>
                            <ellipse fill="#FFFFFF" stroke="#000000" stroke-width="0.25" stroke-miterlimit="10" cx="52.652" cy="74.467" rx="1.613" ry="1.601"/>
                            <ellipse fill="#FFFFFF" stroke="#000000" stroke-width="0.25" stroke-miterlimit="10" cx="50.603" cy="77.415" rx="1.024" ry="1.017"/>
                        </g>
                    </svg>
            </div>
        )
    }
}

export default SVG
